.App {
  text-align: center;
  background: linear-gradient(#FDF4E6, #ddceee);
}

img {
  max-width: 100%;
}

/* TOPBAR */
.topbar {
  width: 100%;
  height: 3.5rem;
  position: fixed;
  top: 0;
  transition-timing-function: ease-in;
  transition: 0.5s;
}
.topbar__color {
  background-color: #F5DEBB;
  color: black;
}
.topbar__layout {
  text-align: justify;
  color: #FDF4E6;

  display: grid;
  grid-template-columns: auto 30%;
  grid-gap: 5%;
}
.topbar__layout > div {
  padding: 20px 0;
  font-size: 1.2em;
}
.logo {
  display: flex;
  flex-flow: wrap;
  justify-content: flex-start;
  gap: 20px;
}
.three-col {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 15%;
  justify-self: end;
  margin-right: 15%;
}
.three-col > p {
  margin-top: 0;
  margin-bottom: 0;
}
.two-col {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 20px;
}

/* HOME */
.decor {
  width: 2.5rem;
  background-color: #F5DEBB;
  margin-left: 48%;
  position: absolute;
  margin-top: -4rem;
  z-index: 1;
  height: 6rem;
}
.textbox {
  background-color: #FFFF;
  border-radius: 20px;
  margin-top: 8%;
  margin-bottom: 10%;
  max-width: 70%;
  padding: 3% 3% 1%;
  box-shadow: 0px 8px rgb(196, 195, 186);
}
.textbox > p {
  font-size: larger;
  font-weight: 500;
}
.text {
  text-align: left;
  font-size: larger;
  font-weight: 500;
}
.line {
  width: 100%;
  height: 120px;  
  border: solid 5px rgb(95, 80, 11);
  border-color: transparent transparent #69540c transparent;
  border-radius: 90%/100% 200px;
  position: absolute;
}
.three-col-hello {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.flags {
  display: inline-flex;
  font-size: large;
  font-weight: 600;
  color: #4b4b4b;
  position: relative;
}

/* PROJECT */
.project {
  display: grid;
  grid-template-rows: auto;
  justify-content: space-around;
}
.project > div {
  justify-content: space-around;
  background-color: #FBF8F4;
  border-radius: 20px;
  margin: 8%; 
  padding: 5%;
  max-height: 70%;
  box-shadow: 0px 8px rgb(224, 222, 202);
}
.project > div:hover {
  background-color: #eee7e0;
}


/* CASE */
.main {
  padding: 3% 20%;
  margin: 0;
  max-width: 80%;
  text-align: left;
  font-size: larger;
  background-color: white;
}
.case {
  text-align: left;
  font-size: larger;
  display: grid;
}
.header {
  display: grid;
  grid-template-columns: auto auto auto;
  background-color:#2B70C9;
  color: white;
  max-width: 100%;
  padding: 100px;
  grid-gap: 50px;
}
.more{
  display: grid;
  grid-template-columns: auto auto;
  text-align: left;
  grid-gap: 0 40px;
  /* margin: 50px; */
}
.persona {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.persona > div {
  background-color: #FBF8F4;
  border-radius: 20px;
  position: relative;
  max-width: 80%;
  margin: 30px 0 30px 0; 
  box-shadow: 0px 10px rgb(235, 231, 197);
  padding: 3% 5%;
  z-index: 1;
}
.phones {
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-between;
  align-items: justify;
}
.phones > img {
  max-height: 620px;
  border-radius: 40px;
}

.btn {
  color: white;
  font-weight: 800;
  font-size: large;
  background-color: black;
  padding: 10px;
  text-align: center;
  border-radius: 10px;
  max-width: 250px;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 
    sans-serif;
}
.btn:hover{
  color: white;
}

.mid {
  text-align: center;
  align-items: center;
  justify-content: space-evenly;
  background-color: white;
}


.gallery{
  display: flex; 
  flex-flow: row wrap;
  justify-content: space-around;
}
.gallery > img {
  margin: 3%;
  max-width: 30%;
}