body {
  margin: auto;
  text-align: center;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(#FDF4E6, #CBDAE5);
}

div {
  margin: auto;
}

p {
  color: #4b4b4b;
  font-size: 1.2rem;
}

a {
  text-decoration: none;
  color: black;
}
a:hover {
  color:#4b4b4b;
}

strong {
  color: black;
  font-weight: 800;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 1.5rem;
  font-weight: 800;
}

ul {
  padding: 0;
  margin: 2px;
}

code {
  font-family:  source-code-pro, Menlo, Monaco, Consolas, 'Nunito',
    monospace;
}

